button[data-start], button[data-stop] {
  cursor: pointer;
  background-color: #6fb1db;
  border: 1px solid #1126b2;
  border-radius: 10px;
  margin: 50px auto;
  padding: 10px;
  font-size: 24px;
  display: block;
  box-shadow: 0 0 0 1px #100909;
}

button:disabled {
  opacity: .5;
  cursor: not-allowed;
  box-shadow: none;
  color: #d0d8d5;
  background-color: #969a9c;
  border-color: #505265;
  box-shadow: 0 0 0 1px #9d5f5f;
}

.timer {
  flex-direction: row;
  justify-content: center;
  gap: 10px;
  margin-top: 50px;
  display: flex;
}

.field {
  border: 1px solid #07080e;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  font-family: Verdana;
  font-size: 30px;
  font-weight: bold;
  display: flex;
}

#datetime-picker {
  text-align: center;
  margin: 0 auto;
  font-size: 50px;
  display: block;
}

.label {
  font-family: Verdana;
  font-size: 15px;
}

